.CardButtonContainer {
  width: calc(100% + 16px);
  position: relative;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: end;
      align-items: flex-end;
  height: 52px;
  /* there is also 4px margin on the button */
  padding: 0 8px 8px 8px; 
  margin-top: 16px;
}



